import React from 'react';
import { selectVideo } from '../state/actions';
import { useDispatch } from 'react-redux';
import VideoThumbnail from './VideoThumbnail';
import styled from 'styled-components';

const Wrapper = styled.div`
    background: #282828;
    border-left: 1px solid #191919;
    border-right: 1px solid #191919;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    overflow: scroll;
    margin-left: 40px;
    max-width: 320px;
    width: 100%;

    .heading {
        align-items: flex-end;
        background: #323232;
        box-shadow: 0 9px 14px -8px #000;
        color: #FFF;
        display: flex;
        justify-content: center;
        min-height: 50px;
        padding-bottom: 10px;
        position: sticky;
        top: 0;
    }

    .heading h2 {
        font-weight: 200;
        margin: 0;
    }

    @media (max-width: 1000px) {
        width: 250px;
    }

    @media (max-width: 750px) {
        border: none;
        margin: 0 auto;
        max-width: 100%;
        width: 100%;

        .heading {
            background: #232323;
            border-bottom: 1px solid #000;
            box-shadow: none;
            min-height: 60px;
            padding-bottom: 20px;
            width: 100%;
        }
    }
`;

function VideoList({ videoSelected, videos }) {
    const dispatch = useDispatch();

    return (
        <Wrapper>
            <div className="heading">
                <h2>Up Next</h2>
            </div>
            {
                videos.map((video, i) => {
                    return (
                        <VideoThumbnail
                            key={`${video.name}-thumbnail`}
                            videoIndex={i}
                            name={video.name}
                            videoSelected={videoSelected}
                            onClick={() => {
                                dispatch(selectVideo(i));
                            }}
                            src={video.thumbnail}
                        />
                    );
                })
            }
        </Wrapper>
    );
};

export default VideoList;
