import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import store from './state/store';
import { initializeVideoData } from './state/actions';
import actionType from './state/actionTypes';
import videoData from './video-data.json';

import './index.css';
import App from './App';

const sortedVideos = videoData.videos.sort((a, b) => {
    return a.name.localeCompare(b.name);
});

store.dispatch(initializeVideoData(sortedVideos, actionType.HYDRATE_VIDEO_DATA));

ReactDOM.render(
    <Provider store={store}>
        <App />
    </Provider>,
    document.getElementById('root')
)
