import React, { useEffect, useState } from 'react';
import {
    Player,
    ControlBar,
    ReplayControl,
    ForwardControl,
    CurrentTimeDisplay,
    TimeDivider,
    PlaybackRateMenuButton,
    VolumeMenuButton,
    BigPlayButton
} from 'video-react';
import HLSSource from './HLSSource';
import styled from 'styled-components';

const Wrapper = styled.div`
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    max-width: 1200px;
    padding-left: 25px;
    width: 100%;

    .heading {
        align-items: center;
        display: flex;
        height: 60px;
    }

    .heading h1 {
        color: #FFF;
        font-weight: 400;
        margin: 0;
    }

    .video-react {
        box-shadow: 0 2px 14px -5px rgba(0, 0, 0, 0.5);
        font-family: "Helvetica Neue", Arial, sans-serif;
        padding-top: calc(100vh - 80px) !important;
    }

    .video-react .video-react-video {
        max-height: 100vh;
    }

    @media (max-width: 1000px) {
        max-width: calc(100% - 270px);
    }

    @media (max-width: 750px) {
        max-width: 100vw;
        margin-bottom: 30px;
        padding: 0 15px;

        .video-react {
            padding-top: 75% !important;
        }

        .video-react .video-react-video {
            max-height: 400px;
        }
    }
`;

function VideoRectangle({ videoSelected, videos }) {
    const [videoSource, setVideoSource] = useState('');
    const [initialLoad, setInitialLoad] = useState(true);

    useEffect(() => {
        setVideoSource(`${videos[videoSelected].path}`);

        // let the source load with
        // initial state before updating
        setTimeout(() => {
            setInitialLoad(false);
        }, 100);
    }, [videoSelected, videos]);

    return (
        <Wrapper>
            <div className="heading">
                <h1>{videos[videoSelected].name}</h1>
            </div>
            <Player
                muted
                autoPlay
            >
                <BigPlayButton position="center" />
                <HLSSource
                    isVideoChild
                    initialLoad={initialLoad}
                    src={videoSource}
                />
                <ControlBar>
                    <ReplayControl seconds={10} order={1.1} />
                    <ForwardControl seconds={30} order={1.2} />
                    <CurrentTimeDisplay order={4.1} />
                    <TimeDivider order={4.2} />
                    <PlaybackRateMenuButton rates={[5, 2, 1, 0.5, 0.1]} order={7.1} />
                    <VolumeMenuButton disabled />
                </ControlBar>
            </Player>
        </Wrapper>
    );
};

export default VideoRectangle;
