import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
    box-sizing: border-box;
    color: #FFF;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    padding: 15px 30px 20px;
    transition: 0.1s all ease;

    &:hover {
        background: rgba(21, 72, 116, 0.25);
    }

    &.active-thumb {
        background: rgba(21, 72, 116, 0.15);

        .thumb-name {
            background: #999;
            color: #000;
        }
    }

    .thumb-name {
        background: #333;
        box-shadow: 0 3px 7px -4px rgba(0, 0, 0, 0.6);
        padding: 10px 12px 12px;
        width: 100%;
    }

    @media (max-width: 1000px) {
        padding: 12px 10px;
    }

    @media (max-width: 750px) {
        flex-direction: row;

        img {
            box-shadow: 0 3px 7px -4px rgba(0, 0, 0, 0.6);
        }

        .thumb-name {
            font-size: 1.1rem;
        }
    }
`;

function VideoThumbnail({
    src,
    name,
    videoIndex,
    videoSelected,
    onClick
}) {
    return (
        <Wrapper
            className={(videoIndex === videoSelected) ? 'active-thumb' : ''}
            onClick={onClick}
        >
            <img alt='name' src={src} />
            <span className="thumb-name">{name}</span>
        </Wrapper>
    );
};

export default VideoThumbnail;
